<template>
    <div id="secondcomponent">
        <h1>I am another page</h1>
        <a>written by {{author}}</a>
        <p>第二个</p>
        <div class="box-card">
              <div slot="header" class="clearfix">
                <h2 style="line-height: 36px; color: #20A0FF">豆瓣电影排行榜</h2>
              </div>
              <div v-for="article in articles" class="text item">
                {{article.title}}
              </div>
          </div>
    </div>
</template>
<script>
export default{
    data(){
        return {
            author:"第二个第二个",
            articles:[],
        }
    },
    mounted(){
        this.$http.jsonp('http://api.douban.com/v2/movie/top250?count=10',{},{
        headers:{},
        emulateJSON:true
        }).then(res=>{
            this.articles=res.data["subjects"]
        },err=>{console.log()})
    }
}
</script>


